@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@400;600&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: mediumspringgreen;
}

a:hover,
a:focus {
  color: white;
}

body {
  font-family: "Rubik", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  color: white;
  max-width: 800px;
  max-height: 800px;
  margin: 0 auto;
  font-size: 1.4rem;
}
.home-link {
  font-size: 1.55rem;
  font-weight: 500;
}

html {
  font-size: 62.5%;
}

.App {
  padding: 2rem;
  text-align: left;
}

.won-gold {
  background-color: #c19d42;
}

.won-silver {
  background-color: #7e8085;
}

.won-bronze {
  background-color: #583e2c;
}

.no-wins {
  background-color: #333842;
}

.NotFound{width:800px}.NotFound .not-found{text-align:center}
.RaceDetailCard{display:grid;grid-template-columns:1fr 1fr;padding:10px;padding-bottom:40px;margin-bottom:20px}.RaceDetailCard .race-venue,.RaceDetailCard .additional-detail{margin-top:10px}.RaceDetailCard .race-date,.RaceDetailCard .race-location{margin-top:4px}.RaceDetailCard .additional-detail{margin-top:5px;font-size:1.1rem}.RaceDetailCard .additional-detail>p{margin-top:2px}.RaceDetailCard .race-result{text-align:right;align-self:top}.RaceDetailCard .race-result>p,.RaceDetailCard .race-result h3{margin-top:5px;margin-bottom:5px}.RaceDetailCard a{color:#7fffbf}.RaceDetailCard a:hover,.RaceDetailCard a:focus{color:#fff}
.RaceSmallCard{padding:10px;padding-bottom:50px;margin-bottom:20px}.RaceSmallCard .race-date{margin-top:5px}.RaceSmallCard .race-location{margin-top:5px}.RaceSmallCard>h3,.RaceSmallCard h4{margin-top:5px}.RaceSmallCard a{color:#7fffbf}.RaceSmallCard a:hover,.RaceSmallCard a:focus{color:#fff}
.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:auto 1fr auto;grid-column-gap:10px;grid-row-gap:10px}.TeamPage .team-name-section{grid-column:span 3;justify-self:start;align-self:end}.TeamPage .team-name{font-size:4rem;margin-bottom:20px}.TeamPage .performance-chart{justify-self:right;padding:25px;text-align:center}.TeamPage .race-detail-section{grid-column:span 4}.TeamPage .more-link{align-self:center;justify-self:center;font-size:1.5rem}
.YearSelector{text-align:right;list-style-type:none}.YearSelector li{margin-top:10px;margin-bottom:10px;font-size:1.2rem}
.RacesPage{display:grid;grid-template-columns:1fr 6fr;grid-template-rows:1fr}.RacesPage .year-selector{justify-self:start;padding-top:80px}.RacesPage .page-heading{margin:20px auto}
.ResultCard{display:grid;grid-template-columns:40px 140px 155px 60px repeat(3, 1fr) 40px;padding-left:20px;padding-right:20px}.ResultCard p{padding-top:5px;font-size:1.1rem}
.RaceResultsPage{width:800px}.RaceResultsPage .page-heading{margin-top:10px}.RaceResultsPage .race-detail{display:grid;grid-template-columns:1fr;margin-bottom:15px}.RaceResultsPage .race-venue,.RaceResultsPage .additional-detail{margin-top:10px}.RaceResultsPage .race-date,.RaceResultsPage .race-location{margin-top:4px}.RaceResultsPage .additional-detail{margin-top:5px;font-size:1.1rem}.RaceResultsPage .additional-detail>p{margin-top:2px}.RaceResultsPage .results-header{color:aqua;margin-top:20px}.RaceResultsPage .cards-header{display:grid;grid-template-columns:40px 140px 155px 60px repeat(3, 1fr) 40px;border:2px solid aqua;padding-left:20px;padding-right:20px;margin-top:10px;margin-bottom:10px}.RaceResultsPage .cards-header>p{padding-top:10px;padding-bottom:10px;justify-self:left;font-size:1.1rem}.home-link{padding:0}
.TeamTile{height:70px;text-align:center;border:1px solid #fff;padding-top:5px}
.HomePage{width:900px}.HomePage .app-name{font-size:2rem;margin:30px auto}.HomePage #dashboard{font-size:2.5rem;font-weight:200;color:aqua}.HomePage .team-grid{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-gap:10px;gap:10px}
